

































import { MDCDialog } from '@material/dialog'

export default
  name: 'language-dialog'


  props:
    value: Boolean



  data: ->
    dialog: Object

    languages: [
        name: 'English'
        value: 'en'
      ,
        name: 'Purtuguês'
        value: 'pt'
      ,
        name: 'Pусский'
        value: 'ru'
      ,
    ]



  methods:
    setLanguage: (language) ->
      @$root.language = language


    close: ->
      @$emit 'input', false



  watch:
    value: ->
      if @value then @dialog.open()
      else @dialog.close()



  mounted: ->
    @dialog = new MDCDialog document.querySelector '#languageDialog'

